@import 'reset.css';

@font-face {
  font-family: 'apercu-mono-regular';
  src: url('./apercu/apercu-mono-regular.eot');
  src: url('./apercu/apercu-mono-regular.eot?#iefix') format('embedded-opentype'),
    url('./apercu/apercu-mono-regular.woff2') format('woff2'),
    url('./apercu/apercu-mono-regular.woff') format('woff'),
    url('./apercu/apercu-mono-regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@import url(//spoqa.github.io/spoqa-han-sans/css/SpoqaHanSans-kr.css);

* {
  font-family: 'apercu-mono-regular', 'Spoqa Han Sans', 'Sans-serif';
  letter-spacing: -0.5px;
  word-break: break-word;
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none;
  &::-webkit-scrollbar {
    display: none;
  }
}

button {
  border: none;
  outline: none;
  padding: 0;
  margin: 0;
  background-color: transparent;
  border-radius: 0;
  cursor: pointer;
}

h4 {
  font-size: 40px;
  @media screen and (max-width: 1280px) {
    font-size: 32px;
  }
  @media screen and (max-width: 780px) {
    font-size: 24px;
  }
}

h5 {
  font-size: 32px;
  @media screen and (max-width: 1280px) {
    font-size: 26px;
  }
  @media screen and (max-width: 780px) {
    font-size: 18px;
  }
}

h6 {
  font-size: 28px;
  @media screen and (max-width: 1280px) {
    font-size: 24px;
  }
  @media screen and (max-width: 780px) {
    font-size: 18px;
  }
}

p {
  font-size: 24px;
  @media screen and (max-width: 1280px) {
    font-size: 20px;
  }
  @media screen and (max-width: 780px) {
    font-size: 16px;
  }
}

.loadingBlk {
  position: fixed;
  width: 100%;
  height: 100%;
  opacity: 1;
  background-color: #000;
  z-index: 1300;
  &.finishLoading {
    animation: opacityToZero 2s ease 0.2s;
  }
}

header {
  display: none;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  box-sizing: border-box;
  padding: 32px 124px;
  @media screen and (max-width: 1280px) {
    padding: 28px 88px;
  }
  @media screen and (max-width: 780px) {
    padding: 24px 27px;
  }
  background-color: #000000;
  z-index: 1400;
  .logo {
    width: 216px;
    a img {
      width: 100%;
    }
    @media screen and (max-width: 1280px) {
      width: 160px;
    }
    @media screen and (max-width: 780px) {
      width: 122px;
    }
  }
  .uiWrapper {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    a {
      margin: 0;
      padding: 0;
      display: block;
    }
    button {
      font-size: 28px;
      text-decoration: underline;
      color: #fff;
      @media screen and (max-width: 1280px) {
        margin-left: 48px;
      }
      &.modalUiButton {
        margin-left: 0;
        @media screen and (max-width: 1280px) {
          font-size: 20px;
        }
        @media screen and (max-width: 780px) {
          display: block;
          position: fixed;
          text-decoration: none;
          white-space: nowrap;
          margin-left: 0px;
          padding: 17px 72px;
          font-size: 20px;
          width: calc(100% - 64px);
          bottom: 24px;
          left: 50%;
          transform: translate(-50%, 0);
          background-color: #000;
          box-sizing: border-box;
          border-radius: 4px;
          &.lineType {
            border: solid 1px #7e8183;
          }
        }
      }
      &.goEvent {
        margin-left: 56px;
        @media screen and (max-width: 1280px) {
          margin-left: 32px;
          font-size: 18px;
        }
        @media screen and (max-width: 780px) {
          margin-left: 0;
        }
      }
      &.langToggle {
        font-size: 24px;
        text-decoration: none;
        margin-left: 72px;
        @media screen and (max-width: 1280px) {
          font-size: 20px;
          margin-left: 48px;
        }
        @media screen and (max-width: 780px) {
          margin-left: 24px;
          font-size: 18px;
        }
        color: #c4cdd5;
      }
    }
  }
}

.fixedBG {
  position: fixed;
  z-index: -999;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background-color: #ff8400;
  video {
    width: 100%;
    height: 100%;
    object-fit: cover;
    @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
      position: absolute;
      width: 100%;
      height: auto;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
}

.section {
  &.fullHeight {
    position: relative;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  &.titleWrapper {
    // @media screen and (max-width: 780px) {
    //   justify-content: flex-start;
    //   padding-top: 220px;
    // }

    .title {
      width: 1017px;
      @media screen and (max-width: 1280px) {
        width: 780px;
      }
      @media screen and (max-width: 780px) {
        width: 85%;
      }
      img {
        width: 100%;
        height: auto;
      }
    }
    h5 {
      margin-top: 72px;
      @media screen and (max-width: 1280px) {
        margin-top: 40px;
        line-height: 1.85;
        letter-spacing: -1px;
        text-align: center;
      }
      @media screen and (max-width: 780px) {
        margin-top: 24px;
        text-align: center;
        width: 314px;
        line-height: 1.33;
        letter-spacing: -1px;
      }
    }
    .scrollDown {
      width: 56px;
      @media screen and (max-width: 1280px) {
        width: 40px;
      }
      a img {
        width: 100%;
      }
      position: fixed;
      bottom: 24px;
      animation: upAndDown 1.2s ease infinite;
    }
  }
  &.decsiptionWrapper {
    // @media screen and (max-width: 780px) {
    //   justify-content: flex-start;
    //   padding-top: 220px;
    // }

    .description {
      width: 960px;
      @media screen and (max-width: 1280px) {
        width: 780px;
      }
      @media screen and (max-width: 780px) {
        width: 85%;
      }
      h5 {
        line-height: 1.33;
        letter-spacing: -1.5px;
        &:nth-of-type(n + 2) {
          margin-top: 48px;
          @media screen and (max-width: 1280px) {
            margin-top: 32px;
            line-height: 1.5;
          }
          @media screen and (max-width: 780px) {
            margin-top: 24px;
            line-height: 1.44;
          }
        }
      }
    }
  }
  &.speakerWrapper {
    background-color: black;
    overflow: hidden;
    .speakers {
      h4 {
        width: 960px;
        @media screen and (max-width: 1280px) {
          width: 780px;
        }
        @media screen and (max-width: 780px) {
          width: 85%;
        }
        margin: 0 auto;
      }
      .speakerList {
        padding: 120px 56px 0;
        @media screen and (max-width: 780px) {
          padding: 80px 24px 0;
        }
        display: flex;
        flex-wrap: nowrap;
        overflow-x: auto;
        &::-webkit-scrollbar {
          display: none;
        }
        @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
          -ms-overflow-style: none;
        }
        .likePadding {
          display: block;
          padding-left: 56px;
          @media screen and (max-width: 780px) {
            padding-left: 32px;
          }
        }
        .speakerCard {
          flex: 0 0 auto;
          width: 400px;
          @media screen and (max-width: 1280px) {
            width: 320px;
          }
          @media screen and (max-width: 780px) {
            width: 200px;
          }
          &:nth-of-type(n + 2) {
            margin-left: 24px;
            @media screen and (max-width: 780px) {
              margin-left: 16px;
            }
          }
          .speakerProfileImg {
            width: 100%;
            padding-top: 500px;
            @media screen and (max-width: 1280px) {
              padding-top: 385px;
            }
            @media screen and (max-width: 780px) {
              padding-top: 240px;
            }
            background-size: 105%;
            background-position: center center;
            background-repeat: no-repeat;
            background-blend-mode: multiply;
            transition: all 0.3s ease;
            &.thuan {
              background-image: url('./img/thuan.jpg');
            }
            &.andrew {
              background-image: url('./img/andrew.jpg');
            }
            &.rajorshi {
              background-image: url('./img/raj.jpg');
            }
            &.beerang {
              background-image: url('./img/beerang.jpg');
            }
            &.wooni {
              background-image: url('./img/wooni.jpg');
            }
            &.shiny {
              background-image: url('./img/shiny.jpg');
            }
            &.alpong {
              background-image: url('./img/alpong.jpg');
            }
            &.bin {
              background-image: url('./img/bin dong.jpg');
            }
            &.duckie {
              background-image: url('./img/duckie.jpg');
            }
            &.zonghan {
              background-image: url('./img/zonghan.jpg');
            }
            &.labong {
              background-image: url('./img/labong.jpg');
            }
            &.yan {
              background-image: url('./img/yan.jpg');
            }
            &.tiiger {
              background-image: url('./img/tiiger.jpg');
            }
            &.cinnamon {
              background-image: url('./img/cinnamon.jpg');
            }
            &.timothy {
              background-image: url('./img/timothy.jpg');
            }
            &:hover {
              background-color: #ff8400;
              background-size: 120%;
            }
          }
          .speakerName {
            margin-top: 32px;
            @media screen and (max-width: 1280px) {
              margin-top: 24px;
            }
            @media screen and (max-width: 780px) {
              margin-top: 32px;
            }
          }
          .speakerTitle {
            margin-top: 4px;
            line-height: 1.25;
            padding-left: 32px;
            @media screen and (max-width: 780px) {
              padding-left: 16px;
            }
            color: #878d93;
          }
        }
      }
    }
  }
  &.blkBg {
    background-color: #000;
    color: #fff;
    padding: 300px 0;
    @media screen and (max-width: 780px) {
      padding: 200px 0;
    }
    .contents {
      width: 960px;
      @media screen and (max-width: 1280px) {
        width: 780px;
      }
      @media screen and (max-width: 780px) {
        width: 85%;
      }
      margin: 0 auto;
      h4 {
        margin-bottom: 92px;
      }
      .singleContentWrapper {
        padding: 48px 0;
        @media screen and (max-width: 1280px) {
          padding: 40px 0;
        }
        @media screen and (max-width: 780px) {
          padding: 32px 0;
        }
        position: relative;
        overflow: hidden;

        &::after {
          content: '';
          opacity: 0;
          position: absolute;
          bottom: 0;
          width: 100%;
          height: 1px;
          background-color: #878d93;
        }
        &.show::after {
          animation-duration: 0.7s;
          animation-iteration-count: 1;
          animation-delay: 0.3s;
          transition-timing-function: ease;
          animation-fill-mode: forwards;
          animation-name: slideLeft;
        }
        .singleContent {
          line-height: 1.5;
          &.hoverable {
            transition: all 0.3s ease;
            cursor: pointer;
            &:hover {
              color: #ff8400;
            }
          }
        }
        &.show .singleContent {
          animation-duration: 1s;
          animation-iteration-count: 1;
          animation-delay: 0;
          transition-timing-function: ease;
          animation-fill-mode: forwards;
          animation-name: fadeInUp;
        }
      }
    }
  }
  &.sessionWrapper {
    .singleContentWrapper:last-of-type::after {
      display: none;
    }
    .singleContent {
      font-weight: 400;
      @media screen and (max-width: 780px) {
        text-align: left;
      }
      &::after {
        content: '';
        display: block;
        clear: both;
        @media screen and (max-width: 780px) {
          clear: left;
        }
      }
      .time {
        float: left;
        word-break: normal;
        @media screen and (max-width: 780px) {
          width: 25%;
        }
      }
      .sessionTitle {
        float: left;
        padding-left: 48px;
        @media screen and (max-width: 780px) {
          padding-left: 36px;
        }
        width: 760px;
        @media screen and (max-width: 1280px) {
          width: 615px;
        }
        @media screen and (max-width: 780px) {
          width: 75%;
        }
        box-sizing: border-box;
      }
    }
  }
  &.faqWrapper {
    p.faqSubTitle {
      margin-top: 148px;
      font-weight: 700;
      @media screen and (max-width: 780px) {
        margin-top: 96px;
      }
    }
    .singleContentWrapper {
      &.noPadding {
        padding: 0 0 1px 0;
        margin-top: 56px;
      }
      .singleContent {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        .singleFaqTitle {
          width: 840px;
          font-weight: 700;
          @media screen and (max-width: 1280px) {
            width: 750px;
          }
          @media screen and (max-width: 780px) {
            width: 80%;
          }
        }
        button.showDetail {
          position: relative;
          width: 26px;
          height: 26px;
          font-weight: 400;
          @media screen and (max-width: 1280px) {
            width: 18px;
            height: 18px;
          }
          @media screen and (max-width: 780px) {
            width: 16px;
            height: 16px;
          }
          margin-left: auto;
          &::before {
            content: '';
            position: absolute;
            background-color: #fff;
            top: 11px;
            height: 4px;
            @media screen and (max-width: 1280px) {
              top: 7.5px;
              height: 3px;
            }
            @media screen and (max-width: 780px) {
              top: 7px;
              height: 2px;
            }
            left: 0;
            width: 100%;
          }
          &.toMinus::after {
            transform: rotate(0deg);
          }
          &::after {
            content: '';
            position: absolute;
            background-color: #fff;
            top: 11px;
            height: 4px;
            @media screen and (max-width: 1280px) {
              top: 7.5px;
              height: 3px;
            }
            @media screen and (max-width: 780px) {
              top: 7px;
              height: 2px;
            }
            left: 0;
            width: 100%;
            transform: rotate(90deg);
            transition: all 0.3s ease;
          }
        }
        .faqDetail {
          width: 840px;
          max-height: 0px;
          overflow: hidden;
          transition: all 0.7s ease;
          @media screen and (max-width: 1280px) {
            width: 750px;
          }
          @media screen and (max-width: 780px) {
            width: 100%;
          }
          letter-spacing: -0.6px;
          span {
            display: block;
            height: 16px;
          }
        }
      }
    }
  }
  &.byeWrapper {
    padding: 240px 0;
    box-sizing: border-box;
    height: auto;
    @media screen and (max-width: 780px) {
      padding: 160px 0;
    }

    h4,
    h5,
    h6 {
      width: 960px;
      font-weight: 400;
      @media screen and (max-width: 1280px) {
        width: 810px;
      }
      @media screen and (max-width: 780px) {
        width: 90%;
        text-align: left;
      }
    }
    h6 {
      line-height: 1.57;
      letter-spacing: -1px;
      @media screen and (max-width: 780px) {
        font-size: 16px;
      }
      &.eventDetail {
        margin-top: 8px;
        @media screen and (max-width: 1280px) {
          margin-top: 4px;
        }
      }
      &.howTo {
        margin-top: 32px;
        @media screen and (max-width: 1280px) {
          margin-top: 16px;
        }
        @media screen and (max-width: 780px) {
          margin-top: 24px;
          display: flex;
        }
      }
      &.eventPeriod {
        @media screen and (max-width: 780px) {
          display: flex;
        }
      }
      &.lastNotice {
        margin-top: 72px;
        @media screen and (max-width: 1280px) {
          margin-top: 56px;
        }
        @media screen and (max-width: 780px) {
          margin-top: 48px;
        }
      }
      span {
        &.bolder {
          margin-right: 24px;
          @media screen and (max-width: 1280px) {
            margin-right: 16px;
            flex: 0 0 auto;
          }
          @media screen and (max-width: 780px) {
            margin-right: 8px;
            flex: 0 0 auto;
          }
        }
        a {
          color: #000;
        }
      }
    }
    .bolder {
      font-weight: 700;
      &.firstEvent {
        margin-top: 120px;
        @media screen and (max-width: 1280px) {
          margin-top: 88px;
        }
        @media screen and (max-width: 780px) {
          margin-top: 72px;
        }
      }
      &.secondEvent {
        margin-top: 72px;
        @media screen and (max-width: 1280px) {
          margin-top: 56px;
        }
        @media screen and (max-width: 780px) {
          margin-top: 48px;
        }
      }
    }
  }
}

footer {
  background-color: black;
  width: 100%;
  box-sizing: border-box;
  padding: 72px 124px;
  @media screen and (max-width: 1280px) {
    padding: 68px 88px;
    align-items: flex-start;
  }
  @media screen and (max-width: 780px) {
    padding: 108px 24px 144px 24px;
  }
  display: flex;
  justify-content: space-between;
  align-items: center;
  .companyLogo {
    width: 180px;
    @media screen and (max-width: 1280px) {
      width: 156px;
    }
    @media screen and (max-width: 780px) {
      width: 100px;
      padding-top: 20px;
    }
    img {
      width: 100%;
      height: auto;
    }
  }
  .footerUi {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    @media screen and (max-width: 780px) {
      flex-direction: column;
      align-items: flex-end;
    }
    p {
      color: #fff;
      display: flex;
      align-items: center;
      &.privacyPolicy {
        cursor: pointer;
        opacity: 0.6;
        font-size: 20px;
        @media screen and (max-width: 1280px) {
          font-size: 16px;
        }
        @media screen and (max-width: 780px) {
          font-size: 12px;
          padding-bottom: 8px;
        }
      }
      &:last-of-type {
        margin-left: 56px;
        @media screen and (max-width: 1280px) {
          margin-left: 40px;
        }
        @media screen and (max-width: 780px) {
          margin-left: 0px;
        }
      }
      span {
        font-size: 32px;
        margin-right: 12px;
        @media screen and (max-width: 1280px) {
          font-size: 24px;
          margin-right: 8px;
        }
        @media screen and (max-width: 780px) {
          font-size: 16px;
          margin-right: 4px;
        }
      }
    }
  }
}

@keyframes upAndDown {
  0% {
    opacity: 1;
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    transform: translateY(25%);
  }
}

@keyframes slideLeft {
  0% {
    opacity: 0;
    transform: translate3d(100%, 0, 0);
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0%, 0);
  }
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translate3d(0, 50%, 0);
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0%, 0);
  }
}

@keyframes opacityToZero {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

.MuiDialog-root.scheduleModalWrapper {
  * {
    font-family: 'apercu-mono-regular', 'Spoqa Han Sans', 'Sans-serif' !important;
  }
  .MuiBackdrop-root {
    background-color: rgba(0, 0, 0, 0.8); // 팝업 배경 색상
    @media screen and (max-width: 780px) {
      background-color: rgba(0, 0, 0, 1);
    }
  }
  .MuiDialog-container {
    align-items: flex-start; // 팝업 수직 정렬 수정
    max-height: calc(100vh - 126px);
    margin-top: 126px;
    @media screen and (max-width: 780px) {
      max-height: calc(100vh - 72px);
      margin-top: 72px;
      &::after {
        position: fixed;
        bottom: 0;
        left: 0;
        z-index: 100;
        content: '';
        width: 100vw;
        height: 140px;
        background: linear-gradient(
          to bottom,
          rgba(0, 0, 0, 0) 0%,
          rgba(0, 0, 0, 0.5) 17%,
          rgba(0, 0, 0, 0.53) 18%,
          rgba(0, 0, 0, 1) 35%,
          rgba(0, 0, 0, 1) 100%
        );
      }
    }
    .MuiPaper-root {
      margin: 0;
      width: 800px;
      height: 760px;
      padding: 0 56px;
      @media screen and (max-width: 1280px) {
        width: 720px;
        height: 694px;
        padding: 0 48px;
      }
      @media screen and (max-width: 780px) {
        width: 100%;
        height: 100%;
        padding: 0 24px;
      }
      max-width: none;
      box-sizing: border-box;
      background-color: #000;
      color: #fff;
      &::before {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 100;
        content: '';
        width: 100%;
        height: 84px;
        @media screen and (max-width: 1280px) {
          height: 80px;
        }
        @media screen and (max-width: 780px) {
          height: 56px;
        }
        background: linear-gradient(
          to top,
          rgba(0, 0, 0, 0) 0%,
          rgba(0, 0, 0, 0.5) 17%,
          rgba(0, 0, 0, 0.53) 18%,
          rgba(0, 0, 0, 1) 35%,
          rgba(0, 0, 0, 1) 100%
        );
      }
      &::after {
        position: absolute;
        bottom: 0;
        left: 0;
        z-index: 100;
        content: '';
        width: 100%;
        height: 84px;
        @media screen and (max-width: 1280px) {
          height: 80px;
        }
        @media screen and (max-width: 780px) {
          display: none;
        }
        background: linear-gradient(
          to bottom,
          rgba(0, 0, 0, 0) 0%,
          rgba(0, 0, 0, 0.5) 17%,
          rgba(0, 0, 0, 0.53) 18%,
          rgba(0, 0, 0, 1) 35%,
          rgba(0, 0, 0, 1) 100%
        );
      }
      .MuiDialogContent-root {
        padding: 0;
        font-weight: 400;
        letter-spacing: -0.3px;
        overflow-y: auto;
        .scheduleTitle {
          font-size: 28px;
          font-weight: 700;
          line-height: 1.43;
          margin-top: 84px;
          margin-bottom: 32px;
          @media screen and (max-width: 1280px) {
            font-size: 24px;
            margin-top: 80px;
          }
          @media screen and (max-width: 780px) {
            font-size: 18px;
            margin-top: 56px;
            margin-bottom: 24px;
          }
        }
        .scheduleContent {
          font-size: 22px;
          @media screen and (max-width: 1280px) {
            font-size: 18px;
          }
          @media screen and (max-width: 780px) {
            font-size: 16px;
          }
          line-height: 1.75;
          &:nth-of-type(n + 2) {
            margin-top: 22px;
            @media screen and (max-width: 1280px) {
              margin-top: 18px;
            }
            @media screen and (max-width: 780px) {
              margin-top: 16px;
            }
          }
        }
        .speakerWrapper {
          padding-left: 68px;
          @media screen and (max-width: 780px) {
            padding-left: 32px;
          }
          line-height: 1.5;
          font-size: 20px;
          @media screen and (max-width: 1280px) {
            font-size: 18px;
          }
          @media screen and (max-width: 780px) {
            font-size: 16px;
          }
          .speakerName {
          }
          .speakerTitle {
          }
          .speakerDescription {
            margin-top: 8px;
            color: #878d93;
          }
          &:nth-of-type(n + 3) {
            margin-top: 8px;
            padding-bottom: 16px;
          }
          &:last-of-type {
            padding-bottom: 72px;
          }
        }
      }
      .MuiDialogActions-root {
        padding: 0;
        button.close {
          position: absolute;
          z-index: 100;
          top: 16px;
          right: 16px;
          width: 48px;
          height: 48px;
          @media screen and (max-width: 1280px) {
            width: 40px;
            height: 40px;
          }
          @media screen and (max-width: 1280px) {
            top: 8px;
            right: 16px;
            width: 32px;
            height: 32px;
          }
          img {
            width: 100%;
            height: 100%;
          }
        }
      }
    }
  }
}

.privacyContent {
  line-height: 1.5;
  letter-spacing: 0.3px;
  margin-top: 84px;
  @media screen and (max-width: 1280px) {
    margin-top: 80px;
  }
  @media screen and (max-width: 1280px) {
    margin-top: 56px;
  }
  table {
    border: 1px solid #fff;
    font-size: 14px;
    border-collapse: collapse;
    td {
      text-align: center;
      vertical-align: middle;
      border: 1px solid #fff;
      padding: 4px;
    }
  }
  a {
    color: #fff;
  }
  .privacyLast {
    padding-bottom: 72px;
  }
  .lefted {
    text-align: left;
  }
}
